.authorize__title {
    color: #fff;
    text-align: center;
    width: 358px;
    font-weight: 900;
    font-size: 24px;
    line-height: 22px;
    margin: 0;
    margin-bottom: 54px;
}

@media screen and (max-width: 1024px) {
.popup__title {
  width: calc(238px + (358 - 238) * ((100vw - 320px)) / (1024 - 320));
  font-size: calc(18px + (24 - 18) * ((100vw - 320px)) / (1024 - 320));
}
}

@media screen and (max-width: 530px) {
.popup__title {
  margin-bottom: 75px;
}
}