.popup__container {
  box-sizing: border-box;
  position: relative;
  max-width: 430px;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 34px 36px 37px;
}

@media screen and (max-width: 530px) {
  .popup__container {
    padding: 25px 22px;
  }
}


